import React, { useEffect, useState } from "react";
import { SubjectSlider } from "../Component/SubjectSlider";
import { CourseSlider } from "../Component/CourseSlider";
import { Course } from "../Component/commonComponent/Course";
import { useSelector } from "react-redux";
import Axios from "axios";

export const Linhvuc = () => {
    const [listCourse, setListCourse] = useState([]);
    const auth = useSelector((state) => state.userStatus.auth);
    const fetchCourse = async () => {
        const Courses = await Axios.get("http://localhost:5000/api/course/get");
        setListCourse(Courses.data);
    };
    useEffect(() => {
        fetchCourse();
        return () => {
            //
        };
    }, [auth]);
    return (
        <div class="container-fluid">
            <div class="px-3">
                <div class="Popular-course">
                    <h3 class="px-3">Các khóa học phổ biến</h3>
                    <CourseSlider type="course" />
                </div>
            </div>
            <div class="px-3">
                <div class="Popular-course">
                    <h3>Toàn bộ khóa học liên quan</h3>
                    <div class="row card-columns mt-4 pt-4 discovery-card-list">
                        {listCourse.map((course) => {
                            return <Course key={course._id} data={course} />;
                        })}
                    </div>
                </div>
            </div>
            <div class="px-3">
                <div class="subjects">
                    <h3>Khám phá</h3>
                    <SubjectSlider />
                </div>
            </div>
        </div>
    );
};
export default Linhvuc